/* Popup style */
.popup-box {
    position: fixed;
    background: #0797b46c;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
  }
  .box {
    position: relative;
    width: 20%;
    margin: 0 auto;
    height: auto;
    max-height: 70vh;
    margin-top: calc(100vh - 85vh - 20px);
    background: #fff;
    border-radius: 20px;
    padding: 20px;
    overflow: auto;
    background-color: #007991;
  }
  .close-icon {
    content: 'x';
    cursor: pointer;
    position: fixed;
    right: calc(40% - 23px);
    top: calc(100vh - 85vh - 29px);
    background: #ededed;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    line-height: 20px;
    text-align: center;
    border: 1px solid #999;
    font-size: 18px;
  }